import React from 'react';
import Component from 'react-pure-render/component';
import Button from 'components/Button';
// import linkify from 'components/linkify';
import text from 'data/text.yml';

export default class Points extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showed: false,
    };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    if (this.root) {
      const rect = this.root.getBoundingClientRect();
      if (rect.top < window.innerHeight / 2) {
        this.setState({ showed: true });
        window.removeEventListener('scroll', this.handleScroll);
      }
    }
  }

  render() {
    const { showed } = this.state;
    const points = text.about.points[__LANG__];
    return (
      <div className={styles.root} ref={(root) => this.root = root}>
        <div className={styles.iphoneSection}>
          <div className={showed ? styles.iphoneVisible : styles.iphone}>
            <img src={require('img/home/iphone.jpg')} />
          </div>
        </div>
        <div className={styles.listSection}>
          <h3>{points.title}</h3>
          <ul>
            {points.list.map((point, i) =>
              <li key={i}>{point}</li>
            )}
          </ul>

          <Button href='#' component='a' theme='yellowGray' icon='doc'>
            {text.about.download[__LANG__]}
          </Button>
        </div>
      </div>
    );
  }

}



