import React from 'react';
import { Link } from 'react-router';
import Component from 'react-pure-render/component';
import KindbeetleIcon from 'icons/logo-kindbeetle.svg';
import CloseIcon from 'icons/close.svg';
import MenuIcon from 'icons/menu.svg';
import Langs from 'components/Langs';
import Footer from 'components/Footer';
import links from 'data/links.yml';
import text from 'data/text.yml';

const items = {
  [links.home[__LANG__]]: text.menu.whatWeDo[__LANG__],
  [links.clientsLink[__LANG__]]: text.menu.clients[__LANG__],
  [links.teamLink[__LANG__]]: text.menu.team[__LANG__],
  [links.feedbackLink[__LANG__]]: text.menu.feedback[__LANG__],
};

export default class Menu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opened: false,
    };
  }

  openMenu = () => this.setState({ opened: true })
  closeMenu = () => this.setState({ opened: false })

  render() {
    const { opened } = this.state;

    return (
      <div className={styles.root}>
        <button
          className={styles.open}
          onClick={this.openMenu}
        >
          <MenuIcon />
        </button>

        {opened &&
          <div className={styles.content}>
            <header>
              <KindbeetleIcon width={189} height={21} />

              <button
                className={styles.close}
                onClick={this.closeMenu}
              >
                <CloseIcon />
              </button>
            </header>

            <Langs />

            <ul className={styles.items}>
              {Object.keys(items).map(url =>
                <li key={url}>
                  <Link
                    to={url}
                    onClick={this.closeMenu}
                  >
                    {items[url]}
                  </Link>
                </li>
              )}
            </ul>

            <footer>
              <Footer />
            </footer>
          </div>
        }
      </div>
    );
  }
}



