import React from 'react';
import Component from 'react-pure-render/component';
// import linkify from 'components/linkify';
import text from 'data/text.yml';

const images = [
  require('img/home/vdnh.svg'),
  require('img/home/yandex.svg'),
  require('img/home/vtb.svg'),
  require('img/home/aeroflot.svg'),
  require('img/home/amway.svg'),
  require('img/home/innopolis.svg'),
  require('img/home/innova.svg'),
  require('img/home/rzd.svg'),
  require('img/home/mvideo.svg'),
];

export default class Clients extends Component {

  render() {
    return (
      <div className={styles.root}>
        <h2 className={styles.title}>{text.clientsTitle[__LANG__]}</h2>

        <ul className={styles.logos}>
          {images.map(src =>
            <li key={src}>
              <img src={src} />
            </li>
          )}
        </ul>
      </div>
    );
  }

}



